<template>
    <div class="certify">
        <h1 class="title">艺术家认证</h1>
        <ul class="certify-text">
            <li>
                <h3>1、什么人可以进行艺术家认证？</h3>
                <p>只要您有足够多的优质的，原创的图片、视频、音频作品，并通过原点展示出来，您就可以获得原点网的艺术家认证。</p>
            </li>
            <li>
                <h3>2、艺术家认证有什么好处？</h3>
                <p>认证为原点网的艺术家，您的昵称后方将显示艺术家的认证标识，并且可以制作自己的艺术家名片，这将可能为您带来更多的粉丝以及额外的商业合作。</p>
            </li>
            <li>
                <h3>3、艺术家认证有费用吗？</h3>
                <p>艺术家认证是完全免费的。</p>
            </li>
            <li>
                <h3>4、什么情况下会被取消艺术家认证？</h3>
                <p>（1） 如被认证的艺术家账户一年之内无任何有效的作品上传，将被自动取消艺术家认证。</p>
                <p>（2） 被认证的艺术家在平台内出现任何的违规现象，将有可能随时失去艺术家认证资格。</p>
                <p>（3） 被认证的艺术家在社会生活中，出现重大的违法行为，或严重影响自身名誉的事件，以及存在巨大争议的违反社会道德的事件或行为，将有可能随时失去艺术家认证资格。</p>
            </li>
        </ul>
        
        <o-button :text="'艺术家认证'" :classStyle="'main-btn'" class="certify-btn" @btnClick="goToCertify" v-show="!artistFlag"></o-button>
        <o-button :text="'您已认证为圆点艺术家'" :classStyle="'gray-btn'" class="over-btn" v-if="artistFlag"></o-button>

        <!-- 艺术家认证弹窗 -->
        <certify-popup v-if="certifyFlag" @goToCertify="goToCertify"></certify-popup>

    </div>
</template>
<script>

    import OButton from '../component/button.vue';
    import CertifyPopup from '../Popup/Certify/certify.vue'
    export default {
        data() {
            return {
                certifyFlag: false, // 艺术家认证弹窗
                artistFlag: false, // 认证为艺术家标识
            }
        },
        methods: {
            goToCertify (value) {
                this.certifyFlag = !this.certifyFlag;
                this.artistFlag = value;
            }
        },
        components: {
            OButton,
            CertifyPopup

        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">
    .certify {
        .title {
            font-size: x-large;
            padding-bottom: 20px;
            text-align: center;
        }
        .certify-text {
            li {
                margin-bottom: 10px;
                h3 {
                    padding: 10px 0;
                }
            }
        }
        .certify-btn, .over-btn {
            width: 160px;
            height: 40px;
            margin: 100px auto 10px;
        }
        .over-btn {
            width: 200px;
        }
    }
</style>


