<template>
    <upgrade-step :header="header" 
                :stepText="stepText" 
                :helpMessage="helpMessage"
                :uploadSuccess="uploadSuccess" 
                :uploadFailure="uploadFailure"
                @goToUpgrade="goToCertify"
                >

        <!-- 第一部分：填写认证信息 -->
        <form slot="firstStep" class="modify-form">
            <div class="form-control">
                <label class="label-title">真实姓名：</label>
                <input class="input-control" type="text" name="username" placeholder="输入您的真实姓名" />
            </div>
            <div class="form-control">
                <label class="label-title">身份证号：</label>
                <input type="text" placeholder="请输入您的身份证号" class="input-control w245" />
            </div>
            <div class="form-control upload-ID">
                <label class="label-title">身份证正面照片：</label>
                <div class="bg">
                    <input type="file" class="input-control" />
                    <span class="upload">点击上传</span>
                </div>
            </div>
        </form>

        <!-- 第二部分：上传认证资料 -->
        <div slot="secondStep" class="pay-tab">
            <form class="modify-form">
                <div class="form-control">
                    <label class="label-title">认证身份：</label>
                    <input class="input-control w245" type="text" name="username" placeholder="请输入您需要认证的身份名称" />
                </div>
                <div class="form-control upload-ID">
                    <label class="label-title">上传认证资料：</label>
                    <div class="upload-file">
                        <div class="up">
                            <o-button :text="'点击上传'" :classStyle="'main-btn'" class="upload-btn" @btnClick=""></o-button>
                            <input type="file" class="input-control" />
                        </div>
                        <p class="tips">
                            请上传任何您觉得能够体现您认证身份的资料，请以图片或".zip"格式的文件上传，文件大小请控制在500Mb以内
                        </p>
                    </div>
                </div>
            </form>
        </div>

        <!-- 点击确认按钮 -->
        <o-button :text="'确定'" :classStyle="'main-btn'" class="btn" @btnClick="certifySuccess" slot="makeSureBtn"></o-button>
    </upgrade-step>
</template>

<script>
    
    import OButton from '../../component/button.vue';
    import UpgradeStep from '../upgradeStep.vue';
    
    export default {
        data() {
            return {
                header: "艺术家认证", // 升级的标题
                stepText: { // 步骤条的信息
                    "first": "填写认证信息",
                    "second": "上传认证资料"
                },
                helpMessage: "将在7个工作日内审核完成，请耐心等待审核结果", // 认证时提示信息
                uploadSuccess: false, // 上传成功
                uploadFailure: true, // 上传失败
                artistFlag: false, // 升级为艺术家标志
            }
        },
        methods: {

            // 关闭弹窗
            goToCertify() {
                this.$emit('goToCertify');
            },

            // 认证成功
            certifySuccess () {
                this.$emit('goToCertify', !this.artistFlag);
            }
        },
        components: {
            OButton,
            UpgradeStep
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">

            // 第一部分：表格
            .modify-form {
                height: 275px;
                .form-control {
                    width: 94%;
                    height: 32px;
                    margin: 20px auto 0;
                    .label-title {
                        width: 108px;
                        text-align: right;
                        float: left;
                        line-height: 32px;
                        font: {
                            size: small;
                            weight: bold;
                        }
                    }
                    .input-control {
                        display: block;
                        width: 164px;
                        height: 100%;
                        border-radius: 4px;
                        float: left;
                        background-color: #F4F4F4;
                        padding: 0 6px;
                        font-size: 13px
                    }
                    .w245 {
                        width: 245px;
                    }
                }
                .upload-ID {
                    .bg {
                        position: relative;
                        width: 245px; 
                        height: 145px;
                        background: #f1f1f1;
                        background-size: 100% 100%;
                        margin-left: 110px;
                        border-radius: 3px;
                        span.upload {
                            color: #48abff;
                            text-align: center;
                            line-height: 10;
                        }
                        input[type=file] {
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            opacity: 0;
                            -ms-filter: 'alpha(opacity=0)';
                            cursor: pointer;
                        }
                    }

                    .up {
                        position: relative;
                        input[type=file] {
                            width: 93px;
                            height: 30px;
                            position: absolute;
                            left: 109px;
                            top: 0px;
                            z-index: 2;
                            opacity: 0;
                            -ms-filter: 'alpha(opacity=0)';
                            cursor: pointer;
                        }
                        .upload-btn {
                            width: 95px;
                            height: 30px;
                        }
                    }
                    .tips {
                        font-size: x-small;
                        width: 64%;
                        margin: {
                            left: 107px;
                            top: 10px;
                        }
                    }

                }
            }

            .btn {
                width: 78px;
                height: 34px;
                margin: 50px auto;
            }
</style>


